@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,700;0,900;1,700&family=Zen+Dots&display=swap");

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*Landing*/

.landingcontainer {
  background-color: rgb(80, 80, 80);
  width: auto;
  height: 100vh;
}

.landinggroup {
  background-image: url("./img/fondolanding.jpg");
  background-size: contain;
  background-position: 100% 0;
  background-repeat: repeat;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

@keyframes giro {
  0% {
    background-position: 100% 0;
  }
  100% {
    background-position: 0% 0;
  }
}
.landinggroup {
  animation: giro 20s linear infinite;
}

.landing {
  padding: 50px;
  color: white;
  font-size: 25px;
  text-shadow: 1px 1px 2px black, 0 0 1em black, 0 0 0.2em black;
}

.landing2 {
  padding: 50px;
  background-color: rgb(255, 255, 255);
  border-radius: 15px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  margin: 50px;
}
.landingbutton {
  padding: 16px 30px;
  font-size: 17px;
  border: none;
  border-radius: 6px;
  background: #fe5a27;
  color: white;
  margin-top: 20px;
  cursor: pointer;
}
.landingbutton:hover {
  background: #e8ef85;
  box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%);
  transition: 0.3s;
  color: black;
}

/*Home*/

.busquedafiltros {
  padding: 15px;
}

.title {
  padding-top: 30px;
}

.filtros {
  /* background: linear-gradient(180deg, rgba(2,0,36,1) 0%, rgba(24,119,242,1) 0%, rgba(0,212,255,1) 100%); */
  /*background-color: black;*/
  display: flex;
  justify-content: space-between;
  padding: 30px;
}

.inputfiltros {
  margin: 5px;
  font-size: 15px;
  padding: 10px;
  font-weight: 700;
}

.countries {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-around;
  padding: 15px 10%;
}

.nameCard {
  color: white;
  text-shadow: 1px 1px 2px black, 0 0 1em white, 0 0 0.2em white;
  padding-bottom: 20px;
  font-size: 22px;
}

.continentCard {
  color: white;
  text-shadow: 1px 1px 2px black, 0 0 1em white, 0 0 0.2em white;
  padding-bottom: 20px;
  font-size: 16px;
}

.countrycard {
  padding: 15px;
  margin: 15px;
  background-color: white;
  border-radius: 10px;
}

.country {
  width: 350px;
  height: 400px;
  display: flex;
  flex-direction: column;
  position: relative;
  /* background: linear-gradient(180deg, rgba(2,0,36,1) 0%, rgba(24,119,242,1) 0%, rgba(0,212,255,1) 100%); */
  background-image: radial-gradient(
    circle at 50% 106.21%,
    #ffff48 0,
    #ffff34 10%,
    #ffff22 20%,
    #ffef11 30%,
    #ffd403 40%,
    #f5b700 50%,
    #e49b03 60%,
    #d3830a 70%,
    #c56e10 80%,
    #b75c15 90%,
    #ac4d19 100%
  );
  margin-bottom: 50px;
  border-radius: 10px;
  padding: 15px;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.country h3 {
  font-size: 30px;
}
.country img {
  box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%);
  object-fit: cover;
  max-width: 280px;
  max-height: 280px;
  justify-content: center;
  align-items: center;
}
.country img:hover {
  transform: scale(1.1);
  z-index: 1;
  transition: 0.3s;
}

.detail {
  background-color: white;
  align-items: center;
  position: sticky;
  font-size: 17px;
}
.searchbutton {
  padding: 10px 22px;
  font-size: 15px;
  border: 3px solid black;
  border-radius: 6px;
  background: #fe5a27;
  font-weight: 700;
  color: black;
  margin: 5px;
  cursor: pointer;
}

.searchbutton:hover {
  background: #e8ef85;
  box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%);
  transition: 0.3s;
  font-weight: 700;
}

.activitybutton {
  padding: 10px 22px;
  font-size: 15px;
  border: 3px solid black;
  border-radius: 6px;
  background: #fe5a27;
  font-weight: 700;
  color: black;
  cursor: pointer;
  display: inline-flex;
  position: absolute;
  text-align: right;
  top: 20px;
  right: 5%;
}

.activitybutton:hover {
  background: #e8ef85;
  box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%);
  transition: 0.3s;
  font-weight: 700;
}

.reloadbutton {
  padding: 5px 11px;
  font-size: 12px;
  border: 3px solid black;
  border-radius: 6px;
  background: #fe5a27;
  color: black;
  font-weight: 700;
  margin: 5px;
  cursor: pointer;
}

.reloadbutton:hover {
  background: #e8ef85;
  transition: 0.3s;
}

.searchinput {
  padding: 10px;
  border-radius: 6px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 30%), 0 8px 16px rgb(0 0 0 / 30%);
}

.ordertext {
  padding: 16px 30px;
  font-size: 10px;
  border: 3px solid black;
  border-radius: 6px;
  background: #fe5a27;
  margin-top: 20px;
  font-weight: 700;
  color: black;
}

.ordertext:hover {
  background: #e8ef85;
  transition: 0.3s;
}

.orderbutton {
  padding: 5px;
  border-radius: 6px;
  cursor: pointer;
  color: #fe5a27;
  background-color: black;
}
.paginado {
  display: inline-block;
}

.title h1 {
  text-shadow: 1px 1px 2px black, 0 0 1em black, 0 0 0.2em black;
  color: #e8ef85;
  padding-bottom: 10px;
}

.title h3 {
  text-shadow: 1px 1px 2px black, 0 0 1em white, 0 0 0.2em white;
  color: #fe5a27;
}

.paginadoButton {
  border: 3px solid black;
  background-color: #fe5a27;
  color: black;
  padding: 5px 12px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 6px;
}

.activeBoton {
  border: none;
  background-color: #e8ef85;
  color: black;
  padding: 5px 12px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 6px;
}

.paginadoButton:hover {
  background: #e8ef85;
  transition: 0.3s;
}

.countryDetail {
  background-image: url("./img/fondocountries.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  justify-content: center;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.countryDetailDiv {
  background-color: rgba(255, 255, 255, 0.562);
  height: auto;
  margin: 5% 0%;
  padding: 7px;
  border-radius: 10px;
  border: 5px solid #308ec6;
  line-height: 1.5;
  font-family: "Lato", sans-serif;
  font-weight: 900;
  color: #04325b;
}

.countryDetailDiv h2 {
  display: flex;
  justify-content: center;
  font-size: 30px;
}

.activitydetail {
  display: flex;
  justify-content: center;
  align-items: center;
}

.detailbutton {
  padding: 10px 22px;
  font-size: 15px;
  border: none;
  border-radius: 6px;
  background: #032849;
  color: white;
  margin: 5px;
  cursor: pointer;
}
.detailbutton:hover {
  background: #276b92;
  transition: 0.3s;
}

.detailflag {
  display: flex;
  max-height: 250px;
  position: 50%;
  text-align: center;
  margin: auto;
}

.spandetail {
  padding-right: 5px;
}

/* FORM */

.cardActivity {
  background-image: radial-gradient(
    circle at 50% 106.21%,
    #ffff48 0,
    #ffff34 10%,
    #ffff22 20%,
    #ffef11 30%,
    #ffd403 40%,
    #f5b700 50%,
    #e49b03 60%,
    #d3830a 70%,
    #c56e10 80%,
    #b75c15 90%,
    #ac4d19 100%
  );
  width: 40%;
  align-items: center;
  margin: 0 30%;
  margin-top: 50px;
  height: 600px;
  justify-content: center;
  border-radius: 20px;
  padding: 0 10px;
  box-shadow: 0 0 20px goldenrod;
  border: 3px solid black;
}

.cardActivity h1 {
  text-shadow: 1px 1px 2px black, 0 0 1em white, 0 0 0.2em white;
  padding-top: 50px;
}

.activities-form {
  padding-top: 50px;
  line-height: 2;
  display: flex;
  flex-direction: column;
}

.cardActivity input::-webkit-input-placeholder,
input,
select {
  text-align: center;
}

.activities-button {
  padding: 5px 11px;
  font-size: 12px;
  border-radius: 6px;
  background: black;
  color: #fe5a27;
  font-weight: 700;
  margin: 5px;
  cursor: pointer;
}

.activities-button:hover {
  background: #e8ef85;
  transition: 0.3s;
}

.removeCountry {
  background-color: transparent;
  border: none;
  color: red;
  cursor: pointer;
  font-size: 18px;
}

.activities-form-input,
select {
  background: none;
  border: none;
  outline: none;
  border-bottom: 1px solid black;
}

.activityOption {
  background: orangered;
}

.activities-form-row {
  display: flex;
  flex-direction: column;
}

.activities-list {
  list-style: none;
  display: inline-flex;
}

.activities-submit {
  margin: 20px 50px;
  background: black;
  color: #fe5a27;
  font-weight: 700;
  border-radius: 6px;
  padding: 5px 11px;
  cursor: pointer;
}

.activities-submit:hover {
  background: #fe5a27;
  transition: 0.3s;
  color: white;
}

.fondo-form {
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.loading {
  background: green;
}
